of the year. Among its many honors, Inception received four Academy Awards and eight\n                    nominations, including Best Picture and Best Screenplay. Nolan was recognized by his peers with\n                    D.G.A. and P.G.A. Award nominations, as well as a W.G.A. Award for his work on the film.")]),_vm._v(" "),_c('p',{attrs:{"data-aos":"fade-up"}},[_vm._v("One of the best-reviewed and highest-grossing movies of 2012, The Dark Knight Rises(2012)\n                    concluded Nolan's Batman trilogy. Due to his success rebooting the Batman character, Warner Bros.\n                    enlisted Nolan to produce their revamped Superman movie Man of Steel (2013), which opened\n                    in the summer of 2013. In 2014, Nolan directed, wrote and produced the science fiction epic Interstellar (2014),\n                    starring Matthew McConaughey, Anne Hathaway and Jessica Chastain. Paramount\n                    Pictures and Warner Bros. released the film on November 5, 2014 to positive reviews and strong\n                    box-office results, grossing over $670 million dollars worldwide.")]),_vm._v(" "),_c('p',{attrs:{"data-aos":"fade-up"}},[_vm._v("Nolan currently resides in Los Angeles, California with his wife, producer Emma Thomas, and\n                    their children. Nolan and Thomas also have their own production company, Syncopy.")])]),_vm._v(" "),_c('img',{attrs:{"data-aos":"fade-left","src":require("../assets/christopher-nolan.png"),"alt":""}})])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('footer',[_c('h4',[_vm._v("Mick Jasker Media © 2018")])])}]
