or The Dark Knight,\n                developing promotional websites and trailers highlighting screenshots of Ledger as the Joker. Ledger\n                died on January 22, 2008, some months after the completed filming and six months before the film's\n                release from a toxic combination of prescription drugs, leading to intense attention from the\n                press and movie-going public.")]),_vm._v(" "),_c('p',[_vm._v("A co-production of the United States and the United Kingdom, The Dark Knight was released\n                on July 16, 2008 in Australia, on July 18, 2008, in North America, and on July 25, 2008, in the\n                United Kingdom. Considered by film critics to be one of the best films of the 2000s and\n                one of the best superhero films ever,[6][7] the film received highly positive\n                reviews, particularly for its screenplay, performances (particularly Ledger's), visual effects, and\n                direction, and set numerous records during its theatrical run.[8] The Dark Knight appeared\n                on more critics' top ten lists (287) than any other film of 2008 with the exception of WALL-E,\n                and more critics (77) named The Dark Knight the best film of 2008 than any other film\n                released that year.[9] With over $1 billion in revenue worldwide, it became the highest-grossing\n                film of 2008 and is the 33rd highest-grossing film of all time, unadjusted for\n                inflation (4th at the time of release).[10] The film received eight Academy Award nominations;\n                it won the award for Best Sound Editing and Ledger was posthumously awarded Best\n                Supporting Actor.[11] The Dark Knight Rises, the final film in the trilogy, was released on\n                July 20, 2012.")]),_vm._v(" "),_c('iframe',{attrs:{"width":"560","height":"315","src":"https://www.youtube.com/embed/EXeTwQWrcwY?rel=0&showinfo=0","frameborder":"0","a